<template>
    <div>
        <el-row id="fifthRow">
            <el-col :span="8">
                <el-form-item label="联系电话" prop="phone">
                    <el-input v-model="phone" placeholder="请输入电话" @change="setPhone" clearable></el-input>
                </el-form-item>
            </el-col>

            <el-col :span="8">
                <el-form-item label="QQ" prop="qq">
                    <el-input v-model="qq" placeholder="请输入QQ" @change="setQQ" clearable></el-input>
                </el-form-item>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: 'FifthRow',
        data () {
            return {
                phone: "",
                qq: ""
            }
        },
        methods: {
            setPhone () {
                this.$emit('getPhone', this.phone)
            },
            setQQ () {
                this.$emit('getQQ', this.qq)
            }
        }
    }
</script>

<style lang="stylus" scoped>

</style>


