<template>
    <div id="form"> 
        <el-form :model="ruleForm" :rules="rules"  ref="ruleForm" label-width="100px">

            <first-row @removeName="removeName"></first-row>

            <second-row @getName="getName" @getNumber="getNumber" @getSex="getSex"></second-row>

            <third-row :allProvinces="allProvinces" @getProvince="getProvince" @getAddress="getAddress" @getBirthday="getBirthday"></third-row>

            <fourth-row @getAcademy="getAcademy" @getMajor="getMajor" @getClasses="getClasses"></fourth-row>

            <fifth-row @getPhone="getPhone" @getQQ="getQQ"></fifth-row>

            <sixth-row :allSections="allSections" :allDuties="allDuties" @getSection="getSection" @getDuty="getDuty"></sixth-row>

            <seventh-row @getTimeString="getTimeString"></seventh-row>

            <el-row id="eighthRow" type="flex" justify="center">
                <el-col :span="2">
                    <el-button type="primary" @click="submitForm('ruleForm')" style="width: 100%">提交</el-button>
                </el-col>
            </el-row>

        </el-form>
    </div>
</template>

<script>
    import FirstRow from './formComponents/FirstRow'
    import SecondRow from './formComponents/SecondRow'
    import ThirdRow from './formComponents/ThirdRow'
    import FourthRow from './formComponents/FourthRow'
    import FifthRow from './formComponents/FifthRow'
    import SixthRow from './formComponents/SixthRow'
    import SeventhRow from './formComponents/SeventhRow'
    export default {
        name: 'Form',
        components: {
            FirstRow: FirstRow,
            SecondRow: SecondRow,
            ThirdRow: ThirdRow,
            FourthRow: FourthRow,
            FifthRow: FifthRow,
            SixthRow: SixthRow,
            SeventhRow: SeventhRow
        },
         data() {
            return {
                ruleForm: {
                    name: "",
                    sex: "",
                    number: "",
                    province: "",
                    address: "",
                    date: "",
                    academy: "",
                    major: "",
                    classes: "",
                    phone: "",
                    qq: "",
                    section: "",
                    duty: "",
                    timeString: ""
                },

                allProvinces: [],
                allSections: [],
                allDuties: [],

                rules: {
                    name: [
                        { required: true, message: '请填写名字！', trigger: 'blur' }
                    ],
                    number: [
                        { required: true, message: '请填写学号！', trigger: 'blur' },
                        { len: 10, message: '请填写正确的学号！', trigger: 'blur' }
                    ],
                    sex: [
                        { required: true, message: '请选择性别！', trigger: 'focus' }
                    ],
                    address: [
                        { required: true, message: '请填写地址！', trigger: 'blur' }
                    ],
                    date: [
                        { required: true, message: '请选择出生日期！', trigger: 'blur' }
                    ],
                    academy: [
                        { required: true, message: '请填写学院名！', trigger: 'blur' }
                    ],
                    major: [
                        { required: true, message: '请填写专业！', trigger: 'blur' }
                    ],
                    classes: [
                        { required: true, message: '请填写班级！', trigger: 'blur' }
                    ],
                    phone: [
                        { required: true, message: '请填写手机号！', trigger: 'blur' },
                        { pattern: /^1[3|4|5|7|8][0-9]{9}$/, message: '请填写正确的格式！', trigger: 'blur' }
                    ],
                    qq: [
                        { required: true, message: '请填写QQ号！', trigger: 'blur' },
                        { pattern: /^[1-9][0-9]{4,10}$/, message: '请填写正确的格式！', trigger: 'blur' }
                    ],
                    section: [
                        { required: true, message: '请选择部门！', trigger: 'change' }
                    ],
                    duty: [
                        { required: true, message: '请选择职务！', trigger: 'change' }
                    ],
                    timeString: [
                        { required: true, message: '请填写空余时间表！' }
                    ]
                }
            };
        },
        methods: {
            /**
             * @todo 删除图片时名字清空
             */
             removeName (name) {
                this.ruleForm.name = name;
                // console.log('删除图片后的姓名', this.ruleForm.name)
             },
            /**
             * @todo 获取子组件传来的名字
             */
             getName (name) {
                this.ruleForm.name = name;
                // console.log('姓名', this.ruleForm.name)
             },
             /**
             * @todo 获取子组件传来的学号
             */
             getNumber (number) {
                this.ruleForm.number = number;
                // console.log('学号', this.ruleForm.number)
             },
             /**
             * @todo 获取子组件传来的性别
             */
             getSex (sex) {
                this.ruleForm.sex = sex;
                // console.log('性别', this.ruleForm.sex)
             },

             /**
             * @todo 获取子组件传来的省份
             */
             getProvince (province) {
                this.ruleForm.province = province;
                // console.log('省份', this.ruleForm.province);
                if (this.ruleForm.province === "") {
                    this.$message({
                        message: '请选择籍贯省份！',
                        type: 'error',
                        showClose: true
                    });
                    return console.log("请选择籍贯省份！")
                }
             },
             /**
             * @todo 获取子组件传来的地址
             */
             getAddress (address) {
                this.ruleForm.address = address;
                // console.log('地址', this.ruleForm.address)
             },
             /**
             * @todo 获取子组件传来的生日
             */
             getBirthday (date) {
                this.ruleForm.date = date;
                console.log('生日', this.ruleForm.date)
             },

             /**
              * @todo 获取子组件传来的学院名
              */
             getAcademy (academy) {
                this.ruleForm.academy = academy;
                // console.log('学院名', this.ruleForm.academy)
             },
             /**
              * @todo 获取子组件传来的专业
              */
             getMajor (major) {
                this.ruleForm.major = major;
                // console.log('专业', this.ruleForm.major)
             },
             /**
              * @todo 获取子组件传来的班级名
              */
             getClasses (classes) {
                this.ruleForm.classes = classes;
                // console.log('班级名', this.ruleForm.classes)
             },

             /**
              * @todo 获取子组件传来的手机号
              */
             getPhone (phone) {
                this.ruleForm.phone = phone;
                // console.log('手机号', this.ruleForm.phone)
             },
             /**
              * @todo 获取子组件传来的qq
              */
             getQQ (qq) {
                this.ruleForm.qq = qq;
                // console.log('qq号', this.ruleForm.qq)
             },

             /**
              * @todo 获取子组件传来的部门
              */
             getSection (section) {
                this.ruleForm.section = section;
                // console.log('部门', this.ruleForm.section)
             },
             /**
              * @todo 获取子组件传来的职务
              */
             getDuty (duty) {
                this.ruleForm.duty = duty;
                // console.log('职务', this.ruleForm.duty)
             },

             /**
              * @todo 获取子组件传来的空闲时间代表码串
              */
             getTimeString (timeString) {
                this.ruleForm.timeString = timeString;
                //  console.log('代表码串', this.ruleForm.timeString)
             },

            //提交表单
            submitForm (formName) {
                // 判断省份是否选择
                if (this.ruleForm.province === "") {
                    this.$message({
                        message: '您的籍贯省份未选择，无法提交！',
                        type: 'error',
                        showClose: true
                    });
                    return console.log("籍贯省份未选择！")
                }

                // 表单提交
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        console.log("it can submit");

                        // 表单对象上传
                        this.$axios.post('AddStudentMessage', {
                            name: this.ruleForm.name,
                            sex: this.ruleForm.sex,
                            number: this.ruleForm.number,
                            province: this.ruleForm.province,
                            address: this.ruleForm.address,
                            date: this.ruleForm.date,
                            academy: this.ruleForm.academy + "学院",
                            major: this.ruleForm.major,
                            classes: this.ruleForm.classes + "班",
                            phone: this.ruleForm.phone,
                            qq: this.ruleForm.qq,
                            section: this.ruleForm.section,
                            duty: this.ruleForm.duty,
                            timeString: this.ruleForm.timeString
                        })
                        .then((res) => {
                            console.log(res);
                            this.$alert('点击确认刷新页面。', '提交成功', {
                                confirmButtonText: '确定',
                                callback: action => {
                                    history.go(0)
                                }
                            })
                        })
                        .catch((res) => {
                            console.log(res);
                            this.$message({
                                message: '提交失败！',
                                type: "error",
                                showClose: true
                            })
                        })
                    } else {
                        console.log("it have error.")
                    }
                })
            },
            // 请求列表项
            getInfo () {
                // this.$axios.post('GetJson')
                this.$axios.post('https://easy-mock.com/mock/5bc484581c7b190e5a64ae29/options/staticData')
                .then((res) => {
                    res = res.data
                    if(res.ret && res.data) {
                        this.allProvinces = res.data.allProvinces;
                        this.allSections = res.data.allSections;
                        this.allDuties = res.data.allDuties
                    };
                    // console.log(res);
                    console.log("the request success")
                })
                .catch((res) => {
                    // console.log(res);
                    console.log("the request error");
                    this.$message({
                        message: '列表数据请求失败，请刷新页面！',
                        type: 'error',
                        showClose: true
                    })
                })
            }
        },
        mounted () {
            this.getInfo()
        }
    }
</script>

<style lang="stylus" scoped>
    #form
        display flex
        justify-content center
        background-color #f0f5f7
        .el-form
            padding 1rem
            margin 1rem 0
            border 1px solid #409EFF
            border-radius 10px
            background-color #EBEEF5
            .el-form-item
                margin-bottom .6rem
            >>> .el-form-item__label
                padding 0 .4rem 0 0
</style>


