<template>
    <div>
        <el-row id="secondRow">
            <el-col :span="8">
                <el-form-item label="姓名" prop="name">
                    <el-input v-model="name" placeholder="请输入您的姓名" @change="setName" clearable></el-input>
                </el-form-item>
            </el-col>

            <el-col :span="8">
                <el-form-item label="学号" prop="number">
                    <el-input v-model="number" placeholder="请输入您的学号" @change="setNumber" clearable></el-input>
                </el-form-item>
            </el-col>

            <el-col :span="8" class="thirdCol">
                <el-form-item label="性别" prop="sex">
                    <el-radio-group v-model="sex" @change="setSex">
                        <el-radio label="男">男</el-radio>
                        <el-radio label="女">女</el-radio>
                    </el-radio-group>
                </el-form-item>
            </el-col>          
        </el-row>
    </div>
</template>

<script>
    import bus from '../../../assets/eventBus'
    export default {
        name: 'SecondRow',
        data () {
            return {
                name: "",
                number: "",
                sex: ""
            }
        },
        methods: {
            setName () {
                this.$emit('getName', this.name);
                bus.$emit('getName', this.name)
            },
            setNumber () {
                this.$emit('getNumber', this.number)
            },
            setSex () {
                this.$emit('getSex', this.sex)
            }
        },
        mounted () {
            bus.$on('removeName', (name) => {
                this.name = name
            })
        }
    }
</script>

<style lang="stylus" scoped>
    #secondRow
        .thirdCol >>> .el-form-item__content
            padding-left .6rem
</style>


