<template>
    <div>
        <el-row id="sixthRow">
            <el-col :span="8">
                <el-form-item label="所在部门" prop="section">
                    <el-select v-model="section"
                                placeholder="请选择所在部门"
                                filterable
                                clearable
                                @change="setSection">
                        <el-option v-for="item in allSections"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-col>

            <el-col :span="8">
                <el-form-item label="职务" prop="duty">
                    <el-select v-model="duty"
                                placeholder="请选择职务"
                                filterable
                                clearable
                                @change="setDuty">
                        <el-option v-for="item in allDuties"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: 'SixthRow',
        props:['allSections', 'allDuties'],
        data () {
            return {
                section: "",
                duty: ""
            }
        },
        methods: {
            setSection () {
                this.$emit('getSection', this.section)
            },
            setDuty () {
                this.$emit('getDuty', this.duty)
            }
        }
    }
</script>

<style lang="stylus" scoped>
    #sixthRow >>> .el-select
        width 100%
</style>


