<template>
    <div>
        <el-row id="fourthRow">
            <el-col :span="8">
                <el-form-item label="学院" prop="academy">
                    <el-input v-model="academy" placeholder="请输入学院名" @change="setAcademy" clearable>
                        <template slot="append">学院</template>
                    </el-input>
                </el-form-item>
            </el-col>

            <el-col :span="8">
                <el-form-item label="专业" prop="major">
                    <el-input v-model="major" placeholder="请输入专业" @change="setMajor" clearable></el-input>
                </el-form-item>
            </el-col>

            <el-col :span="8">
                <el-form-item label="班级" prop="classes">
                    <el-input v-model="classes" placeholder="请输入班级" @change="setClasses" clearable>
                        <template slot="append">班</template>
                    </el-input>
                </el-form-item>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: 'FourthRow',
        data () {
            return {
                academy: "",
                major: "",
                classes: ""
            }
        },
        methods: {
            setAcademy () {
                this.$emit('getAcademy', this.academy)
            },
            setMajor () {
                this.$emit('getMajor', this.major)
            },
            setClasses () {
                this.$emit('getClasses', this.classes)
            }
        }
    }
</script>

<style lang="stylus" scoped>

</style>


