<template>
    <div>
        <el-row id="thirdRow">
            <el-col :span="16" class="firstCol">
                <el-form-item label="籍贯" prop="address">
                    <el-input v-model="address"
                                placeholder="请输入具体地址"
                                @change="setAddress"
                                clearable>
                        <el-select v-model="province"
                                    slot="prepend"
                                    placeholder="请选择省份，可搜索"
                                    filterable
                                    clearable
                                    @change="setProvince">
                            <el-option v-for="item in allProvinces"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                            </el-option>
                        </el-select>
                    </el-input>
                </el-form-item>
            </el-col>

            <el-col :span="8" class="secondCol">
                <el-form-item label="出生日期" prop="date">
                    <el-date-picker v-model="date"
                                    type="date"
                                    placeholder="请选择出生日期"
                                    value-format="yyyy-MM-dd"
                                    @change="setBirthday">
                    </el-date-picker>
                </el-form-item>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: 'ThirdRow',
        props: ['allProvinces'],
        data () {
            return {
                address: "",
                province: "",
                date: ""
            }
        },
        methods: {
            setProvince () {
                this.$emit('getProvince', this.province)
            },
            setAddress () {
                this.$emit('getAddress', this.address)
            },
            setBirthday () {
                this.$emit('getBirthday', this.date)
            }
        }
    }
</script>

<style lang="stylus" scoped>
    #thirdRow
        .firstCol >>> .el-select
            width 180px
        .secondCol >>> .el-date-editor
            width 100%
</style>


