<template>
    <div>
        <el-row id="firstRow">
            <el-col :span="24">
                <el-form-item label="照片上传">
                <el-upload action="UpLoad"
                            ref="upload"
                            list-type="picture-card"
                            :auto-upload="false"
                            :limit="1"
                            :on-change="imgChange"
                            :on-preview="handlePreview"
                            :before-remove="beforeRemove"
                            :on-remove="handleRemove"
                            :on-exceed="handleExceed"
                            :on-success="handleSuccess"
                            :on-error="handleError">
                    <i class="el-icon-plus"></i>
                </el-upload>
                <div style="color: #F56C6C">注：请将图片名命名为您的名字再上传！因为当你输入完姓名时这张图片将会自动上传！</div>
                <el-dialog :visible.sync="dialogVisible">
                    <img width="100%" :src="dialogImageUrl" alt="">
                </el-dialog>
            </el-form-item>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import bus from '../../../assets/eventBus'
    export default {
        name: 'FirstRow',
        data () {
            return {
                dialogVisible: false,
                dialogImageUrl: "",
                fileName: "",
                imageUrl: "",
                uploadSuccess: false,
                name: ""
            }
        },
        methods: {
            // 文件状态改变时的钩子函数，添加文件、上传成功和上传失败时都会被调用
            imgChange (file, fileList) {
                // console.log(file, fileList);
                this.fileName = file.name.slice(0, file.name.indexOf('.'));
                this.imageUrl = file.url;
                if (this.name !== "") {
                    this.uploadImage()
                } else {
                    this.$message({
                        message: '请继续填写姓名以便上传图片！',
                        showClose: true
                    });
                    console.log("已有图片等待填姓名。")
                }
            },
            // 图片预览
            handlePreview (file) {
                console.log("图片完全显示！");
                this.dialogImageUrl = file.url;
                this.dialogVisible = true
            },
            // 图片删除前
            beforeRemove (file, fileList) {
                return this.$confirm(`确定移除${file.name}？`, '提示')
            },
            // 图片已删除
            handleRemove (file, fileList) {
                console.log("图片已删除！");
                this.uploadSuccess = false;
                this.fileName = "";
                this.imageUrl = "";
                this.name = "";
                this.$emit('removeName', this.name);
                bus.$emit('removeName', this.name)
            },
            // 图片数量超过限制
            handleExceed (file, fileList) {
                this.$message({
                    message: '只能选一张！',
                    type: 'warning',
                    showClose: true
                })
            },
            // 图片上传成功
            handleSuccess (response, file, fileList) {
                console.log('图片上传成功！', response);
                this.uploadSuccess = true
            },
            // 图片上传失败
            handleError (err, file, fileList) {
                console.log('图片上传失败！', err);
                this.$message({
                    message: '图片上传失败，请刷新页面重来！',
                    type: 'error',
                    showClose: true
                })
            },
            /**
             * @todo 上传图片到服务器
             */
            uploadImage () {
                if (!this.uploadSuccess) {
                    if (this.imageUrl !== "") {
                        // console.log("有图片，可以进行上传。");
                        if (this.fileName !== this.name) {
                            this.$message({
                                message: '您的图片名看来和你的名字不一样哦，请删掉改好名字再重传！',
                                type: 'error',
                                showClose: true
                            });
                            return console.log("图片命名错误！")
                        } else {
                            // console.log("图片上传中......");
                            this.$refs.upload.submit()
                        }
                    } else {
                        this.$message({
                            message: '您还未上传图片！',
                            type: 'error',
                            showClose: true
                        });
                        return console.log("未上传图片！")
                    }
                } else {
                    console.log("您已经上传完图片了，除非您删掉原先图片选择新的图片，否则再改名字是没有用的。")
                }
            }
        },
        mounted () {
            bus.$on('getName', (name) => {
                // console.log('页面间传姓名', name);
                this.name = name;
                if (this.name !== "") {
                    this.uploadImage()
                } else {
                    this.$message({
                        message: '您没填名字！',
                        type: 'error',
                        showClose: true
                    });
                    console.log("您没填名字！")
                }
            })
        }
    }
</script>

<style lang="stylus" scoped>

</style>


